// src/components/login-form.jsx
"use client"

import { useCallback, useState } from "react"
import { signIn } from "next-auth/react"
import { useSearchParams } from "next/navigation"
import { Loader2 } from "lucide-react"

import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"

export function LoginForm({
  className,
  providerId = "dummy-sso",
  providerName = "Mock SSO",
  ...props
}) {
  const [isLoading, setIsLoading] = useState(false)
  const searchParams = useSearchParams()
  const callbackUrl = searchParams?.get("callbackUrl") ?? "/"
  const isOidc = providerId === "oidc"

  const handleSsoLogin = useCallback(async () => {
    setIsLoading(true)
    try {
      await signIn(providerId, { callbackUrl })
    } finally {
      // signIn will redirect, but reset state for safety (e.g. error case)
      setIsLoading(false)
    }
  }, [callbackUrl, providerId])

  return (
    <div className={cn("flex flex-col gap-6", className)} {...props}>
      <Card>
        <CardHeader>
          <CardTitle>Single Sign-On</CardTitle>
          <CardDescription>
            {isOidc
              ? "회사 계정으로 로그인하세요."
              : "개발/테스트용 더미 계정으로 로그인합니다."}
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col gap-4">
            <Button
              type="button"
              onClick={handleSsoLogin}
              disabled={isLoading}
            >
              {isLoading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" aria-hidden="true" />
                  Signing in...
                </>
              ) : (
                "SSO login"
              )}
            </Button>
            <p className="text-center text-sm text-muted-foreground">
              {isOidc
                ? `${providerName}를 통해 인증이 진행됩니다.`
                : "ENV에 AUTH_OIDC_ISSUER, AUTH_OIDC_ID, AUTH_OIDC_SECRET 값을 설정하면 즉시 실제 OIDC 서버로 연결됩니다."}
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
